body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body,
#root {
  width: 100%;
}

.App {
  height: 100%;
}

.dimmer {
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.container {
  width: 400px;
  margin-top: 72px;
  margin-bottom: 72px;
  padding-top: 24px;

  background-color: rgba(255, 255, 255, 0.65);
  border: 2px solid black;
  border-radius: 8px;
}

h2 {
  text-align: center;
  margin-bottom: 48px;
}

.config,
.game {
  margin: 12px auto;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.select-form-control {
  width: 200px;
}

/* This is not a good way of adding margin but I could
   not get it to work in .select-form-control */
.config-select {
  margin-bottom: 24px;
}

button {
  margin-top: 12px;
  margin-bottom: 12px;
}

.timer {
  margin-top: 12px;
  margin-bottom: 12px;

  color: black;
  font-size: xx-large;
}

.track-information {
  margin-top: 12px;
  margin-bottom: 12px;
  text-align: center;
  color: black;
}

.song-name {
  font-weight: bolder;
  font-size: larger;
  text-align: center;
  margin-left: 12px;
  margin-right: 12px;
}

.artists {
  text-align: center;
  margin-left: 12px;
  margin-right: 12px;
}

.footer {
  text-align: center;
  margin-bottom: 24px;
}

